import React, { FormEvent, MutableRefObject, useEffect, useRef, useState } from 'react';
import ReactPlayer from 'react-player';
import styles from './App.module.scss';
import audio from './assets/audio01.mp3';
import voiceKR from './assets/voice01.mp3';
import voiceEN from './assets/voice02.mp3';
// import video from "./assets/video.mp4";
import subtitleKR from './assets/subtitle_kr.vtt';
import subtitleKR2 from './assets/subtitle_kr2.vtt';
import subtitleEN from './assets/subtitle_en.vtt';
import subtitleEN2 from './assets/subtitle_en2.vtt';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import IframeTest from './pages/IframeTest/IframeTest';
import { throttle } from 'lodash';
import OffsetTest from './pages/OffsetTest/OffsetTest';

const video = [
    'http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerFun.mp4',
    'http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ElephantsDream.mp4',
];
const subtitle: {
    [key: string]: string[];
} = {
    kr: [subtitleKR, subtitleKR2],
    en: [subtitleEN, subtitleEN2],
};
const voice: {
    [key: string]: string;
} = {
    kr: voiceKR,
    en: voiceEN,
};

function App() {
    const [play, setPlay] = useState(false);
    const [onBuffer, setOnBuffer] = useState(false);
    const [onAudioBuffer, setOnAudioBuffer] = useState(false);
    const [onAudioBuffer2, setOnAudioBuffer2] = useState(false);

    const [vidoeReady, setVideoReady] = useState(false);
    const [audioReady, setAudioReady] = useState(false);
    const [audioReady2, setAudioReady2] = useState(false);

    const [durationSeconds, setDurationSeconds] = useState(100);
    const [playedSeconds, setPlayedSeconds] = useState(0);
    const playerRef = useRef() as MutableRefObject<ReactPlayer>;
    const audioPlayerRef = useRef() as MutableRefObject<ReactPlayer>;
    const audioPlayerRef2 = useRef() as MutableRefObject<ReactPlayer>;

    const seek = (e: FormEvent<HTMLInputElement>) => {
        const SECONDS =
            Math.min(
                durationSeconds,
                typeof e === 'number' ? e : durationSeconds * 0.01 * +e.currentTarget.value,
            ) - 0.0001;
        playerRef?.current?.seekTo(SECONDS, 'seconds');
        audioPlayerRef?.current?.seekTo(SECONDS, 'seconds');
        audioPlayerRef2?.current?.seekTo(SECONDS, 'seconds');
        setPlayedSeconds(SECONDS);
    };

    const [subtitleLang, setSubtitleLang] = useState('kr');
    // console.log(
    //     `영상 버퍼 : ${onBuffer}, 오디오 버퍼 : ${onAudioBuffer}, 오디오 버퍼2 : ${onAudioBuffer2}`
    // );
    // console.log(subtitle[subtitleLang]);

    useEffect(() => {
        setAudioReady2(false);

        if (!playerRef.current || !audioPlayerRef.current || !audioPlayerRef2.current) return;
        playerRef.current.seekTo(playerRef.current.getCurrentTime(), 'seconds');
        audioPlayerRef.current.seekTo(playerRef.current.getCurrentTime(), 'seconds');
    }, [subtitleLang]);
    useEffect(() => {
        if (!audioReady2) return;
        audioPlayerRef2.current.seekTo(playerRef.current.getCurrentTime(), 'seconds');
        console.log('tste');
    }, [audioReady2]);
    const progressRef = useRef<HTMLDivElement>(null);
    const [percentage, setPercentage] = useState(0);
    const onDragging = useRef(false);
    useEffect(() => {
        window.addEventListener('mouseup', () => {
            if (onDragging.current) onDragging.current = false;
        });
        window.addEventListener(
            'mousemove',
            throttle(
                (e) => {
                    if (!onDragging.current || !progressRef.current) return;
                    const WIDTH = progressRef.current?.clientWidth;
                    const MIN = progressRef.current?.offsetLeft;
                    const MAX = MIN + WIDTH;
                    let newValue = 0;
                    if (e.clientX < MIN) newValue = 0;
                    else if (e.clientX > MAX) newValue = 100;
                    else newValue = ((e.clientX - MIN) / (MAX - MIN)) * 100;
                    setPercentage(newValue);
                    playerRef.current.seekTo(newValue / 100, 'fraction');
                    console.log('move');
                },
                20,
                { trailing: true },
            ),
        );
    }, []);
    useEffect(() => {
        setPercentage((playedSeconds / durationSeconds) * 100);
    }, [playedSeconds, durationSeconds]);

    useEffect(() => {
        function handleMessage(e: MessageEvent<any>) {
            console.log(e);
        }
        window.addEventListener('message', handleMessage, false);
        return () => {
            window.removeEventListener('message', handleMessage);
        };
    }, []);

    function handleUpdateTime(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
        console.log(e);
    }

    const [playingVideoIndex, setPlayingVideoIndex] = useState(0);
    const [useSound, setUseSound] = useState(true);
    const [useSubtitle, setUseSubtitle] = useState(true);

    useEffect(() => {
        if (!playerRef.current.getInternalPlayer()) return;
        if (!useSubtitle) playerRef.current.getInternalPlayer().textTracks[0].mode = 'hidden';
        else playerRef.current.getInternalPlayer().textTracks[0].mode = 'showing';
        // if (!useSubtitle) playerRef.current.getInternalPlayer().childNodes[0].src = null;
        // else playerRef.current.getInternalPlayer().childNodes[0].src = subtitle[subtitleLang];
    }, [useSubtitle]);

    function handlePrevTenSeconds() {
        const CURRENT_TIME =
            playerRef.current?.getCurrentTime() - 10 > 0
                ? playerRef.current?.getCurrentTime() - 10
                : 0;

        playerRef.current?.seekTo(CURRENT_TIME, 'seconds');
        audioPlayerRef.current?.seekTo(CURRENT_TIME, 'seconds');
        audioPlayerRef2.current?.seekTo(CURRENT_TIME, 'seconds');
        if (CURRENT_TIME === 0 && playingVideoIndex !== 0) {
            setPlay(false);
            setTimeout(() => {
                setOpenCounter(true);
            }, 500);
        }
    }

    const [openCounter, setOpenCounter] = useState(false);
    const [counter, setCounter] = useState(3);
    useEffect(() => {
        if (playingVideoIndex === 0) return;
        setTimeout(() => {
            setOpenCounter(true);
        }, 500);
    }, [playingVideoIndex]);
    useEffect(() => {
        if (!openCounter) return;
        setTimeout(() => {
            if (counter > 0) setCounter((prev) => prev - 1);
            else {
                setOpenCounter(false);
                setPlay(true);
                setCounter(3);
            }
        }, 1000);
    }, [openCounter, counter]);
    return (
        <>
            <BrowserRouter>
                <Routes>
                    <Route
                        path='*'
                        element={
                            <>
                                <div className={styles.container}>
                                    <div className={styles.videoWrap}>
                                        {openCounter && (
                                            <div className={styles.counterWrap}>
                                                <span>Get Ready!</span>
                                                <span>{counter !== 0 ? counter : 'Go!'}</span>
                                            </div>
                                        )}
                                        {audioReady && vidoeReady && audioReady2 && (
                                            <div className={`${styles.controlWrap} `}>
                                                <button
                                                    className={`${styles.playBtn} ${
                                                        play ? styles.play : styles.pause
                                                    }`}
                                                    onClick={(e) => {
                                                        e.stopPropagation();
                                                        setPlay((prev) => !prev);
                                                    }}
                                                ></button>
                                                <button
                                                    className={styles.videoBackBtn}
                                                    onClick={handlePrevTenSeconds}
                                                >
                                                    10초 뒤로가기
                                                </button>
                                                <button
                                                    className={styles.subtitleSet}
                                                    onClick={() => setUseSubtitle((prev) => !prev)}
                                                >
                                                    자막
                                                    {useSubtitle ? 'off' : 'on'}
                                                </button>
                                                <button
                                                    className={styles.mikeSet}
                                                    onClick={() => setUseSound((prev) => !prev)}
                                                >
                                                    음성
                                                    {useSound ? 'off' : 'on'}
                                                </button>
                                                <button
                                                    className={`${styles.fullScreenBtn} ${
                                                        document.fullscreenElement
                                                            ? styles.full
                                                            : ''
                                                    }`}
                                                    onClick={() => {
                                                        const element = document.querySelector(
                                                            '.fullScreen',
                                                        ) as HTMLVideoElement & {
                                                            mozRequestFullScreen?: () => void;
                                                            webkitRequestFullScreen?: () => void;
                                                            msRequestFullScreen?: () => void;
                                                            webkitEnterFullScreen?: () => void;
                                                        };
                                                        if (!document.fullscreenElement) {
                                                            if (element?.requestFullscreen)
                                                                element?.requestFullscreen();
                                                            else if (element?.mozRequestFullScreen)
                                                                element?.mozRequestFullScreen();
                                                            else if (
                                                                element?.webkitRequestFullScreen
                                                            )
                                                                element?.webkitRequestFullScreen();
                                                            else if (element?.msRequestFullScreen)
                                                                element?.msRequestFullScreen();
                                                            else if (element?.webkitEnterFullScreen)
                                                                element?.webkitEnterFullScreen();
                                                        } else {
                                                            if (document.exitFullscreen) {
                                                                document.exitFullscreen();
                                                            } else if (
                                                                (document as any)
                                                                    ?.webkitExitFullscreen
                                                            ) {
                                                                (
                                                                    document as any
                                                                )?.webkitExitFullscreen();
                                                            } else if (
                                                                (document as any)
                                                                    .mozCancelFullScreen
                                                            ) {
                                                                (
                                                                    document as any
                                                                )?.mozCancelFullScreen();
                                                            } else if (
                                                                (document as any).msExitFullscreen
                                                            ) {
                                                                (
                                                                    document as any
                                                                )?.msExitFullscreen();
                                                            }
                                                        }
                                                    }}
                                                ></button>
                                                <div
                                                    className={styles.durationBar}
                                                    onClick={(e) => e.stopPropagation()}
                                                >
                                                    <input
                                                        type='range'
                                                        value={
                                                            (playedSeconds / durationSeconds) *
                                                                100 || 0
                                                        }
                                                        step={0.25}
                                                        min={0}
                                                        max={100}
                                                        style={{
                                                            background: `linear-gradient(to right, #4852EC calc(${
                                                                (playedSeconds / durationSeconds) *
                                                                100
                                                            }% + ${
                                                                -9 +
                                                                (18 -
                                                                    (((playedSeconds /
                                                                        durationSeconds) *
                                                                        100) /
                                                                        100) *
                                                                        18)
                                                            }px), #DBDBDB calc(${
                                                                (playedSeconds / durationSeconds) *
                                                                100
                                                            }% + ${
                                                                -9 +
                                                                (18 -
                                                                    (((playedSeconds /
                                                                        durationSeconds) *
                                                                        100) /
                                                                        100) *
                                                                        18)
                                                            }px)`,
                                                        }}
                                                        // onChange={seek}
                                                        onInput={seek}
                                                    />
                                                </div>
                                            </div>
                                        )}
                                        <ReactPlayer
                                            key={playingVideoIndex}
                                            ref={playerRef}
                                            playing={
                                                play &&
                                                (onBuffer || (!onAudioBuffer && !onAudioBuffer2))
                                            }
                                            autoPlay={false}
                                            url={video[playingVideoIndex]}
                                            muted
                                            playsinline
                                            loop={false}
                                            onReady={() => setVideoReady(true)}
                                            onBuffer={() => {
                                                setOnBuffer(true);
                                            }}
                                            onBufferEnd={() => {
                                                setOnBuffer(false);
                                            }}
                                            onDuration={(duration) => {
                                                setDurationSeconds(duration);
                                            }}
                                            onProgress={(progress) => {
                                                setPlayedSeconds(progress.playedSeconds);
                                            }}
                                            onEnded={() => {
                                                setPlay(false);
                                                setPlayingVideoIndex((prev) => prev + 1);
                                                audioPlayerRef?.current?.seekTo(0, 'seconds');
                                                audioPlayerRef2?.current?.seekTo(0, 'seconds');
                                            }}
                                            progressInterval={100}
                                            config={{
                                                file: {
                                                    tracks: [
                                                        {
                                                            kind: 'subtitles',
                                                            src: subtitle[subtitleLang][
                                                                playingVideoIndex
                                                            ],
                                                            srcLang: 'kr',
                                                            default: true,
                                                            label: '자막',
                                                        },
                                                    ],
                                                },
                                            }}
                                        ></ReactPlayer>
                                    </div>
                                    <div className={styles.audioWrap}>
                                        <ReactPlayer
                                            url={audio}
                                            ref={audioPlayerRef}
                                            onEnded={() => console.log('test')}
                                            onReady={() => {
                                                console.log('ready');
                                                setAudioReady(true);
                                            }}
                                            playing={
                                                play &&
                                                (onAudioBuffer || (!onBuffer && !onAudioBuffer2))
                                            }
                                            onBuffer={() => {
                                                setOnAudioBuffer(true);
                                            }}
                                            onBufferEnd={() => {
                                                setOnAudioBuffer(false);
                                            }}
                                            controls
                                            loop={false}
                                            muted={!useSound}
                                        ></ReactPlayer>
                                        <ReactPlayer
                                            url={voice[subtitleLang]}
                                            ref={audioPlayerRef2}
                                            onReady={() => {
                                                console.log('test');
                                                setAudioReady2(true);
                                            }}
                                            playing={
                                                play &&
                                                (onAudioBuffer2 || (!onBuffer && !onAudioBuffer))
                                            }
                                            onBuffer={() => {
                                                setOnAudioBuffer2(true);
                                            }}
                                            onBufferEnd={() => {
                                                setOnAudioBuffer2(false);
                                            }}
                                            controls
                                            loop={false}
                                            muted={!useSound}
                                        ></ReactPlayer>
                                    </div>
                                </div>
                                <select
                                    onChange={(e) => {
                                        setSubtitleLang(e.currentTarget.value);
                                        playerRef.current.getInternalPlayer().childNodes[0].src =
                                            subtitle[e.currentTarget.value][playingVideoIndex];
                                    }}
                                >
                                    <option value='kr'>한국어</option>
                                    <option value='en'>영어</option>
                                </select>

                                {/* <div
                                    className={styles.progressBar}
                                    ref={progressRef}
                                >
                                    <div
                                        className={styles.progressArea}
                                        onClick={handleUpdateTime}
                                    >
                                        <div className={styles.total}>
                                            <div
                                                className={styles.fill}
                                                style={{
                                                    width: `${percentage}%`,
                                                }}
                                            ></div>
                                        </div>
                                    </div>
                                    <div
                                        className={styles.circle}
                                        style={{
                                            left: `calc(${percentage}% - 16px * ${
                                                percentage / 100
                                            } + (-8px + 16px * ${
                                                percentage / 100
                                            }))`,
                                        }}
                                        onMouseDown={() => {
                                            onDragging.current = true;
                                        }}
                                        // onMouseUp={() =>
                                        //     (onDragging.current = false)
                                        // }
                                    ></div>
                                </div> */}
                            </>
                        }
                    />
                    <Route path='/iframe' element={<IframeTest />} />
                    <Route path='/offset-test' element={<OffsetTest />} />
                </Routes>
            </BrowserRouter>
        </>
    );
}

export default App;
